import { SpotLight } from "@react-three/drei"
import React from "react"




export const Light = () => {


    return (
        <>
            <SpotLight
                castShadow
                receiveShadow
                position={[6, 9, 10]}
                attenuation={10}
                distance={150}
                scale={1}
                intensity={0.5}
            >
            </SpotLight>
            <ambientLight intensity={0.015}/>
        </>
    )
}