import React, { useRef, Suspense, useEffect } from 'react';
import * as THREE from 'three'
import { Canvas, extend, useThree, useFrame, useLoader } from 'react-three-fiber';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';
import './App.css';
import { Light } from './components/Light';
import { Physics, RigidBody } from '@react-three/rapier';
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader';
import { MTLLoader } from 'three/examples/jsm/loaders/MTLLoader';
import { useState } from 'react';

extend({ OrbitControls })

const Controls = () => {
  const orbitRef = useRef()
  const { camera, gl } = useThree()

  useFrame(() => {
    orbitRef.current.update()
  })

  return (
    <orbitControls
      args={[camera, gl.domElement]}
      ref={orbitRef}
      maxPolarAngle={-1}
      minPolarAngle={-1}
    />
  )
}




const Scene = ({ left, right, gravity, color, restart }) => {
  const ref = useRef()
  const [type, setType] = useState('kinematicVelocity')
  const [stateLeft, setLeft] = useState(0)
  const [stateRight, setRight] = useState(0)

  const textureArea = useLoader(MTLLoader, 'Area big/Area.mtl');
  const area = useLoader(OBJLoader, 'Area big/Area.obj', (load) => {
    textureArea.preload()
    load.setMaterials(textureArea)
  });

  const textureCoin = useLoader(MTLLoader, 'COIN/COIN_animation_prefinal.mtl')
  const coin = useLoader(OBJLoader, 'COIN/Coin_animation_prefinal.obj', (load) => {
    textureCoin.preload()
    load.setMaterials(textureCoin)
  })

  coin.traverse(function (child) {
    if (child) {
      child.castShadow = true
    }
  })

  area.traverse(function (child) {
    if (child) {
      child.receiveShadow = true
    }
  })

  const clickState = (left, right) => {
      setType('dynamic')
      setLeft(left)
      setRight(right)
  }

  useFrame(() => {
    if (ref.current.parent.rotation.y < -1.50 && color === true && gravity) {
      clickState(left, right)
    }

    if (ref.current.parent.rotation.y > 0.40 && color === false && gravity) {
        clickState(left, right)
    }
  })

  useEffect(() => {
    if (restart) {
      let id = clickState(0,0)
      clearTimeout(id)
      if (ref.current) {
        console.log(ref.current.parent)
        const obj = ref.current.parent
        obj.position.set(0, 0, 0)
        obj.quaternion.set(0, 0, 0, 0)
        setType('kinematicVelocity')
        setLeft(0)
        setRight(0)
      }
    }
  })





  return (
    <group>
      <RigidBody
        colliders='cuboid'
        type={type}
        angularVelocity={[stateLeft, -10, stateRight]}
        angularDamping={0.004}
        mass={1}
      >
        <primitive ref={ref} castShadow receiveShadow object={coin}></primitive>
      </RigidBody>
      <RigidBody mass={0}>
        <primitive reciveShadow object={area}></primitive>
      </RigidBody>
    </group>
  )
}

const App = () => {
  const [left, setLeft] = useState(0)
  const [right, setRight] = useState(0)
  const [color, setColor] = useState()
  const [restart, setRestart] = useState(false)
  const [stateGravityBolean, setStateGravityBolean] = useState(false)

  
  const clickRed = () => {
    let id = setTimeout(() => {
      setLeft(-0.4)
      setRight(-0.4)
      setStateGravityBolean(true)
      setColor(false)
      setRestart(false)
    }, 10500)
    return id
  }

  const clickBlack = () => {
    let id = setTimeout(() => {
      setLeft(-Math.random() * 1.5)
      setRight(-Math.random() * 1.5)
      setColor(true)
      setStateGravityBolean(true)
      setRestart(false)
    }, 10500)
    return id
  }

  const clickRestart = () => {
    const idBlack = clickBlack()
    const idRed = clickRed()
    clearTimeout(idRed)
    clearTimeout(idBlack)
    setColor(null)
    setStateGravityBolean(false)
    setLeft(0)
    setRight(0)
    setRestart(true)
    
  }



  return (
    <div>
      <Suspense>
        <Canvas shadows camera={{ position: [0, 0, 3] }} onCreated={({ gl }) => {
          gl.shadowMap.enabled = true;
          gl.shadowMap.type = THREE.PCFSoftShadowMap;
        }}>
          <Physics gravity={[0, -9.81, 0]}>
            <color attach="background" args={['#202020']} />
            <Scene left={left} right={right} gravity={stateGravityBolean} color={color} restart={restart} />
            <Controls />
          </Physics>
          <Light />
        </Canvas>
      </Suspense>
      <button onClick={() => clickRed()}>RED</button>
      <button onClick={() => clickBlack()}>BLACK</button>
      <button onClick={() => clickRestart()}>RESTART</button>
    </div>
  )
}

export default App
